import React, { useState } from "react";
import Layout from "../components/layout";
import { Country } from "country-state-city";
import { initializeApp } from "firebase/app";
import { collection, addDoc, getFirestore } from "firebase/firestore/lite";
import { useForm } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers/yup";
import { utils } from "../core/helper";
import * as Yup from "yup";
import Loading from "../components/common/loading";
// import "../styles/assets/css/general.css";
// import "../styles/assets/css/style.css";
import blogImg1 from "../styles/assets/images/Blogs/Blog1.png";

const validationSchema = Yup.object().shape({
  name: Yup.string().label("Name").required(),
  email: Yup.string().label("Email").required(),
  country: Yup.string().label("Country").required(),
  mobile: Yup.string().label().required("Required"),
  content: Yup.string().label("Content").required(),
});

const Blog = (props) => {
  const { pageContext } = props;
  console.log("pageContext::", pageContext);
  const [loading, setLoading] = useState(false);

  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm({
    resolver: yupResolver(validationSchema),
  });

  const countries = Country.getAllCountries();
  const app = initializeApp(
    pageContext?.firestore?.data?.site?.siteMetadata?.firestore
  );
  const db = getFirestore(app);

  const handleOnSubmit = async (values) => {
    const { name, email, country, content, mobile } = values;
    const payload = {
      name,
      email,
      country,
      content,
      mobile,
      date: new Date(),
    };
    setLoading(true);
    try {
      const clients = collection(db, "clients");
      await addDoc(clients, payload);
      utils.showSuccssMsg("We will contact/talk to you soon");
      reset();
      setLoading(false);
    } catch (e) {
      utils.showErrMsg("Error adding document");
      console.log("e::", e);
      setLoading(false);
    }
  };

  return (
    <>
      <Layout db={pageContext?.firestore?.data?.site?.siteMetadata?.firestore}>
        {/* <section className="mt-5">
          <div className="container">
            <div className="row ">
              <div className="col-md-12 col-lg-8">
                <div className="row">
                  <div className="blog_img shadow">
                    <img src={pageContext?.data?.image} alt="blog-img" />
                  </div>
                  <h2 className="mt-4 fs-1 mb-0">{pageContext?.data?.title}</h2>
                  <div className="blog_wrapper mb-3">
                    <h4 className="fs-2 mb-3 mt-3 text-dark">
                      {pageContext?.data?.author_name}
                    </h4>
                    <i className="fs-4 text-secondary">
                      Posted On: ({pageContext?.data?.blog_created_date})
                    </i>
                  </div>
                  <div
                    dangerouslySetInnerHTML={{
                      __html: pageContext?.data?.description,
                    }}
                    className="blog-content mt-3"
                  ></div>
                </div>
              </div>
              <div className="col-md-12 col-lg-4">
                <div className="card sticky-top section-cta border-0 contact_us_card">
                  <div className="card-header contact_us-header border-0 bg-transparent d-flex">
                    <h4 className="my-auto">Contact Us</h4>
                  </div>
                  <div className="card-body">
                    <form
                      onSubmit={handleSubmit(handleOnSubmit)}
                      className="cta-form"
                    >
                      <div>
                        <label htmlFor="full-name" className="text-dark">
                          Name
                        </label>
                        <input
                          id="full-name"
                          type="text"
                          placeholder="Enter Your Name"
                          {...register("name")}
                          maxLength={30}
                        />
                        <p className="text-danger mb-0">
                          {errors.name?.message}
                        </p>
                      </div>
                      <div>
                        <label htmlFor="email" className="text-dark">
                          Email
                        </label>
                        <input
                          id="email"
                          type="email"
                          placeholder="me@example.com"
                          {...register("email")}
                          maxLength={50}
                        />
                        <p className="text-danger mb-0">
                          {errors.email?.message}
                        </p>
                      </div>
                      <div>
                        <label htmlFor="select-where" className="text-dark">
                          Country
                        </label>
                        <select name="country" {...register("country")}>
                          <option value="">Please choose one option:</option>
                          {countries.map((ele, ind) => {
                            return (
                              <option key={ind} value={ele.isoCode}>
                                {ele.name}
                              </option>
                            );
                          })}
                        </select>
                        <p className="text-danger mb-0">
                          {errors.country?.message}
                        </p>
                      </div>
                      <div>
                        <label htmlFor="mobile" className="text-dark">
                          Skype / Whatsapp / Mobile
                        </label>
                        <input
                          id="mobile"
                          type="tel"
                          placeholder="Skype / Whatsapp / Mobile"
                          {...register("mobile")}
                          maxLength={50}
                        />
                        <p className="text-danger mb-0">
                          {errors.mobile?.message}
                        </p>
                      </div>
                      <div>
                        <label htmlFor="content" className="text-dark">
                          Content
                        </label>
                        <textarea
                          id="content"
                          placeholder="Type content here"
                          rows={5}
                          className="input"
                          {...register("content")}
                          maxLength={200}
                        ></textarea>
                        <p className="text-danger mb-0">
                          {errors.content?.message}
                        </p>
                      </div>
                      <button
                        disabled={loading}
                        className="btn btn--form mt-3 mx-0 p-3"
                      >
                        Submit {loading && <Loading />}
                      </button>
                    </form>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section> */}
        <div className="blog-bg"></div>
        <div className="margin-top:-300px">
          <div className="container blog-site">
            <div className="row p-5">
              <h1 className="blog-title text-white">
                Unraveling DeFi: An Exclusive Guide To Decentralized Finance
              </h1>
              <p className="text-white">SEPTEMBER 11, 2023 Justtry Team</p>
              <img className="blog-img" src={blogImg1} />
              <div className="blog-const">
                <h1>
                  Unraveling DeFi: An Exclusive Guide To Decentralized Finance
                </h1>
                <p>
                  Have you ever wondered how and why decentralized finance
                  (DeFi) came into existence? It is often said that the launch
                  of Bitcoin in 2019 set the wheels in motion for decentralized
                  finance. The ultimate goal of DeFi was to disrupt existing
                  financial intermediaries, such as Banks. Looking at the
                  current scenario, it won’t be news to conclude that DeFi has
                  paved its way into the global financial arena. Not only has
                  decentralized finance revolutionized the financial sector, but
                  it has also empowered us to perform peer-to-peer transactions
                  on the go. But what does DeFi mean? Let’s take a look.
                </p>
                <h1>Decentralized Finance: The Buzzword of 2023</h1>
                <p>
                  Decentralized finance, a.k.a. DeFi is a fast-emerging
                  financial technology that eliminates middlemen when utilizing
                  any kind of financial service. Blockchain and cryptocurrency
                  act as the backbone technologies that enable DeFi. In layman's
                  terms, Decentralized finance is a blockchain-powered
                  alternative to traditional financial services and
                  applications. You can perform all kinds of services that a
                  bank supports, like borrowing, lending, earning interest, and
                  more. There are far too many benefits to DeFi applications
                  when compared to CeFi ones. Even Binance CEO Changpeng Zhao
                  believes so. The crypto boss shared his conviction on
                  September 1, 2023, that DeFi has the capability to outperform
                  CeFi in trading volumes. Let’s take a deeper dive into how
                  decentralized finance works.
                </p>
                <h1>How Does DeFi Actually Work?</h1>
                <p>
                  As mentioned earlier, decentralized finance utilizes
                  blockchain technology. A blockchain is a distributed ledger
                  technology (DLT) that helps record transaction details in the
                  form of blocks. These blocks are immutable, meaning that
                  transactions can only be viewed and verified but cannot be
                  modified. It is a known fact that blockchain utilizes smart
                  contracts to carry out operations without middlemen. When it
                  comes to DeFi, these smart contracts replace intermediaries
                  when performing financial operations. Smart contracts are not
                  only swift but also help eliminate extra charges that users
                  might have paid to third parties. In order to use
                  decentralized finance, users need applications called dApps.
                </p>
                <h1>Some Crucial Components of DeFi</h1>
                <p>
                  It has already been clearly established that blockchain and
                  smart contracts are key components of decentralized finance.
                  But that’s not all. There exists a list of primary components
                  that make up DeFi as we know it today. Let’s check them out.
                </p>
                <p>
                  <strong>
                    <u>Decentralized Exchanges (DEXs):</u>
                  </strong>
                  These are peer-to-peer marketplaces that connect users
                  directly to carry out crypto-based transactions without any
                  central authority.
                </p>
                <p>
                  <strong>
                    <u>Stablecoins:</u>
                  </strong>
                  These digital currencies are often tied to fiat currencies
                  such as the Indian rupee or the US dollar and are intended to
                  maintain a constant value. They are quite essential to DeFi
                  because they provide stability when it comes to lending and
                  transactional activities.
                </p>
                <p>
                  <strong>
                    <u>Lending and Borrowing Protocols:</u>
                  </strong>
                  DeFi platforms provide lending and borrowing services, letting
                  users earn interest by lending their cryptocurrency or
                  borrowing assets in exchange for collateral.
                </p>
                <p>
                  <strong>
                    <u>Governance Tokens:</u>
                  </strong>
                  Many DeFi protocols feature their own governance tokens, which
                  provide holders with the opportunity to vote on the protocol’s
                  overall development. 
                </p>
                <p>
                  <strong>
                    <u>Wallets and Aggregators:</u>
                  </strong>
                  Different DeFi aggregators and wallet services assist users in
                  interacting with various DeFi platforms and managing their
                  assets.
                </p>
                <p>
                  <strong>
                    <u>Yield Farming:</u>
                  </strong>
                  Users can contribute liquidity to decentralized exchanges as a
                  substitute for awards or fees. This is sometimes referred to
                  as yield farming.
                </p>
                <h1>Why is Decentralized Finance the Future?</h1>
                <p>
                  DeFi is creating an avant-garde financial infrastructure
                  rather than modifying TradFi's current financial
                  infrastructure. The ever-rising popularity of decentralized
                  finance is bringing down traditional finance slowly but
                  surely. The total value locked (TVL) in DeFi protocols has
                  already surpassed $49 billion. Furthermore, DeFi has the
                  potential to provide financing at a more reasonable price than
                  TradFi. That is why DeFi may soon be the preferred solution
                  for businesses to fulfill their credit requirements.
                </p>
                <h1>Conclusion</h1>
                <p>
                  Decentralized finance has way too many benefits when compared
                  to traditional finance. DeFi has the ultimate power to shrink
                  the overall expenditure of TradFi by 80%.  Did you know DLT
                  technology like Blockchain can save TradFi $100 billion
                  annually? With such hefty savings, one can remain assured that
                  DeFi is here to stay. These numbers are enough to launch a
                  decentralized application of your own today. There are
                  numerous DeFi development companies available to help you
                  develop and launch a dApp of your own in a matter of weeks.
                  Just make sure to align with a reliable firm. With 2023 almost
                  at the end, make sure to close out the year with a bang!
                </p>
              </div>
            </div>
          </div>
        </div>
      </Layout>
    </>
  );
};

export default Blog;
